body {
  box-sizing: border-box;
  --text-main-color: #000;
  --text-secondary-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  height: 40px;
}

.nav-list {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav-item:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  color: var(--text-main-color);
  height: 60px;
  line-height: 60px;
  display: inline-block;
}

.nav-link:hover, .is-active {
  color: var(--text-secondary-color);
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--text-secondary-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}
